.drop-menu {
  width: 80px;
  height: 32px;
  margin-left: -50%;
  margin-bottom: -50%; 
  background-color: rgba(255,255,0,.5);
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.drop-menu-item {
  width: 32px;
  background-color: rgba(255,0,0,06);
  cursor: pointer;
  border: solid 1px rebeccapurple;
  margin: 0 1px;
}
