
#sketchpad {

	position:absolute;
	top:0;
	left:0;
	border:0;
	padding:0;
	margin:0;
	background-color: rgba(0,255,0,0.2);
	visibility:hidden;
	/*opacity : 0.5;*/
	z-index:102;

}

.drawing #sketchpad {
	visibility:visible;
}

#sketchpad.sketching {
	visibility:visible;
}

#drag-canvas {
	visibility:hidden;
	z-index: 1;
  position: absolute;
  top: 0px;
  left:0;
  right: 0;
  bottom: 0;
  background-color: transparent;
}

.drawing #drag-canvas {
	visibility:visible;
}
